import axios from "axios";
import { useState } from "react";
import { useNavigate } from "react-router-dom";

function Login() {
	const navigate = useNavigate();

    const [data, setData] = useState({
        username: "",
        password: ""
    });

	const [loginState, setLoginState] = useState({});

    const handleChange = (e) => {
		console.log(e);
        const {name, value} = e.target;
        console.log("name: ", name, "value: ", value);
        //e.target.name & e.target.value
        setData({
            ...data,
            [name]: value
        })
        console.log(data);
    }

    //A + B
    const submit = async(e) => {
		try {
			//A
			const res = await axios.post(`/v2/admin/signin`, data); //01 API
			const { token, expired } = res.data; //02 token
			console.log(res.data);

			// axios.defaults.headers.common["Authorization"] = AUTH_TOKEN; //axiosHeaders(token)

			//tokencookie
			//1 hexTokentoken
			//2 expirednew Date()
			document.cookie = `hexToken=${token}; expires=${new Date(expired)};`;
			// console.log(document.cookie);

			res?.data?.success && navigate("/admin/products");
		}catch(err) {
			// alert("Wrong Account or Password")
			console.log(err.response.data.message);
			setLoginState(err.response.data)
		}
    }

	const enterKey = (e) => {
		console.log(e);
		if (e.key === "Enter") {
			submit();
		}
	};

	return (
		<div className='container py-5'>
			<div className='row justify-content-center'>
				<div className='col-md-6'>
					<h2></h2>

					<div className={`alert alert-danger ${loginState?.message ? "d-block" : "d-none"}`} role='alert'>
						{loginState?.message}
					</div>
					<div className='mb-2'>
						<label htmlFor='email' className='form-label w-100'>
							Email
							<input id='email' className='form-control' name='username' onChange={handleChange} />
						</label>
					</div>
					<div className='mb-2'>
						<label htmlFor='password' className='form-label w-100'>
							
							<input
								type='password'
								className='form-control'
								name='password'
								id='password'
								placeholder='name@example.com'
								onChange={handleChange}
								onKeyDown={enterKey}
							/>
						</label>
					</div>
					<button type='button' className='btn btn-primary' onClick={submit}>
						
					</button>
				</div>
			</div>
		</div>
	);
}

export default Login;
