import { useEffect, useState, useRef, useContext } from 'react';
import axios from "axios";
import { Modal } from "bootstrap";

import CouponModal from '../../components/CouponModal';
import DeleteModal from '../../components/DeleteModal';
import Pagination from '../../components/Pagination';
import { MessageContext, deleteSuccessMessage } from './../../store/messageStore';


const AdminCoupons = () => {
	const [coupons, setCoupons] = useState([]);
	const [pagination, setPagination] = useState({});

	//typemodal
	const [type, setType] = useState("create"); //createedit

	//()
	const [tempCoupon, setTempCoupon] = useState({});

	const [, dispatch] = useContext(MessageContext);

	//bootstrap modal
	const couponModal = useRef(null);
	const deleteModal = useRef(null);

    const api = `/v2/api/${process.env.REACT_APP_API_PATH}`;

	//(expired)
	useEffect(() => {
		couponModal.current = new Modal("#couponModal", {
			backdrop: "static",
		});
		deleteModal.current = new Modal("#deleteModal", {
			backdrop: "static",
		});
		// console.log("productModal.current: ", productModal.current);
		getCoupons();
	}, []);


	//Coupon
	const getCoupons = async (page = 1) => {
		try {
			//B
			const couponRes = await axios.get(`${api}/admin/coupons?page=${page}`); //04 APItoke
			console.log(couponRes);
			setCoupons(couponRes.data.coupons);
			setPagination(couponRes.data.pagination);
		} catch (err) {
			console.log(err);
		}
	};

	//
	const deleteCoupon = async(id) => {
		try {
			const res = await axios.delete(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupon/${id}`);
			// console.log(res);
			if(res.data.success) {
				deleteSuccessMessage(dispatch, res);
				deleteModal.current.hide();
				getCoupons(); //
			}
		}catch(err) {
			console.log(err);
		}
	};

	//CouponModal
	const openCouponModal = (type, item) => {
		setType(type);
		setTempCoupon(item);
		couponModal.current.show();
	};
	const closeModal = () => {
		couponModal.current.hide();
	};

	//DeleteModal
	const openDeleteModal = (item) => {
		setTempCoupon(item);
		deleteModal.current.show();
	};
	const closeDeleteModal = () => {
		deleteModal.current.hide();
	};

	return (
		<div className='p-3'>
			<CouponModal
				closeModal={closeModal}
				getCoupons={getCoupons}
				type={type}
				setTempCoupon={setTempCoupon}
				tempCoupon={tempCoupon}
			/>
			<DeleteModal 
                close={closeDeleteModal} 
                text={tempCoupon.title} 
                handleDelete={deleteCoupon} 
                id={tempCoupon.id} 
            />
			<h3></h3>
			<hr />
			<div className='text-end'>
				<button type='button' className='btn btn-primary btn-sm' onClick={() => openCouponModal("create", {})}>
					
				</button>
			</div>
			<table className='table'>
				<thead>
					<tr>
						<th scope='col'></th>
						<th scope='col'></th>
						<th scope='col'></th>
						<th scope='col'></th>
						<th scope='col'></th>
						<th scope='col'></th>
					</tr>
				</thead>
				<tbody>
					{coupons.map((item) => (
						<tr key={item.id}>
							<td>{item.title}</td>
							<td>{item.percent}</td>
							<td>{new Date(item.due_date).toDateString()}</td>
							<td>{item.code}</td>
							<td>{item.is_enabled ? "" : ""}</td>
							<td>
								<button type='button' className='btn btn-primary btn-sm' onClick={() => openCouponModal("edit", item)}>
									
								</button>
								<button
									type='button'
									className='btn btn-outline-danger btn-sm ms-2'
									onClick={() => openDeleteModal(item)}
								>
									
								</button>
							</td>
						</tr>
					))}
				</tbody>
			</table>

			{/* Pagination */}
			<Pagination pagination={pagination} changePage={getCoupons} />
		</div>
	);
}

export default AdminCoupons;