import { useState, useEffect, useContext } from 'react';
import axios from "axios";
import Input from './Input';
import { MessageContext, handleSuccessMessage, handleErrorMessage } from './../store/messageStore';
import { addZero } from '../store';


const CouponModal = ({ closeModal, getCoupons, type, tempCoupon, setTempCoupon }) => {
	const [tempData, setTempData] = useState({
		title: "",
		is_enabled: 1,
		percent: 80,
		due_date: 1555459200,
		code: "testCode",
	});

	//
	// api --> unix timestamp(ex. 1555459200)
	// new Date() --> 
	// input -->  2024-4-11 (<input>value)
	const [date, setDate] = useState(new Date());
	console.log(date);
    console.log(- true);
    console.log(Boolean([]));

	const [, dispatch] = useContext(MessageContext);

	useEffect(() => {
		if (type === "create") {
			//
			setTempData({
				title: "",
				is_enabled: 1,
				percent: 80,
				due_date: 1555459200,
				code: "testCode",
			});
			setDate(new Date()); //coupon
		} else if (type === "edit") {
			setTempData(tempCoupon);
			setDate(new Date(tempCoupon.due_date)); //couponcoupondue_date
		}
	}, [type, tempCoupon]);

	const handleChange = (e) => {
		//console.log(e)
		const { value, name } = e.target;
		//console.log(typeof +e.target.checked);

		if (["num", "percent"].includes(name)) {
			//
			setTempData({
				...tempData,
				[name]: Number(value),
			});
		} else if (name === "is_enabled") {
			//is_enabled0 or 1
			//e.target.checked
			setTempData({
				...tempData,
				[name]: +e.target.checked, //e.target.checkedtrue/false+0/1
			});
		} else {
			setTempData({
				...tempData,
				[name]: value,
			});
		}
	};

	const submit = async () => {
		try {
			let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupon`;
			let method = "post";

			if (type === "edit") {
				api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupon/${tempCoupon.id}`;
				method = "put";
			}

			console.log("api: ", api);

			const res = await axios[method](api, {
				data: {
					...tempData,
					due_date: date.getTime(), //unix timeStamp
				}, //failed axios
			});
			//
			handleSuccessMessage(dispatch, res);
			closeModal();
			getCoupons(); //
		} catch (err) {
			console.log(err);
			handleErrorMessage(dispatch, err);
		}
	};

	return (
		<div
			id='couponModal'
			className='modal fade'
			tabIndex='-1'
			aria-labelledby='exampleModalLabel'
			aria-hidden='true'
		>
			<div className='modal-dialog modal-lg'>
				<div className='modal-content'>
					<div className='modal-header'>
						<h1 className='modal-title fs-5' id='exampleModalLabel'>
							{type === "create" ? "" : ` ${tempData.title}`}
						</h1>
						<button type='button' className='btn-close' aria-label='Close' onClick={closeModal} />
					</div>
					<div className='modal-body'>
						<Input
							labelText=''
							type='text'
							id='title'
							name='title'
							placeholder=''
							value={tempData.title}
							onChange={handleChange}
						/>
						<div className='row'>
							<div className='col-md-6 mb-2'>
								<Input
									labelText='(%)'
									type='text'
									id='percent'
									name='percent'
									placeholder='(%)'
									value={tempData.percent}
									onChange={handleChange}
								/>
							</div>
							<div className='col-md-6 mb-2'>
								<label className='w-100' htmlFor='due_date'>
									
									<input
										type='date'
										id='due_date'
										name='due_date'
										placeholder=''
										className='form-control mt-1'
										value={`${date.getFullYear().toString()}-${addZero(
											date.getMonth() + 1
										)}-${addZero(date.getDate())}`}
										onChange={(e) => {
											setDate(new Date(e.target.value)); //""
										}}
									/>
								</label>
							</div>
							<div className='col-md-6 mb-2'>
								<Input
									labelText=''
									type='text'
									id='code'
									name='code'
									placeholder=''
									value={tempData.code}
									onChange={handleChange}
								/>
							</div>
						</div>
						<label className='form-check-label' htmlFor='is_enabled'>
							
							<input
								type='checkbox'
								id='is_enabled'
								name='is_enabled'
								placeholder=''
								className='form-check-input'
								checked={!!tempData.is_enabled}  //!!true/false
								onChange={handleChange}
							/>
						</label>
					</div>
					<div className='modal-footer'>
						<button type='button' className='btn btn-secondary' onClick={closeModal}>
							
						</button>
						<button type='button' className='btn btn-primary' onClick={submit}>
							
						</button>
					</div>
				</div>
			</div>
		</div>
	);
};

export default CouponModal;