import { useState, useEffect, useContext } from 'react';
import axios from "axios";
import Input from './Input';
import { MessageContext, handleSuccessMessage, handleErrorMessage } from '../store/messageStore';

const ProductModal = ({ closeProductModal, getProducts, type, tempProduct }) => {
    const [tempData, setTempData] = useState({
			title: "",
			category: "",
			origin_price: 100,
			price: 300,
			unit: "",
			description: "",
			content: "",
			is_enabled: 1, //01
			imageUrl: "",
		});

	//const [message, dispatch] = useContext(MessageContext);
	//messagestate
	//message
	const [, dispatch] = useContext(MessageContext);

	useEffect(() => {
		if(type === "create") {
			//
			setTempData({
				title: "",
				category: "",
				origin_price: 100,
				price: 300,
				unit: "",
				description: "",
				content: "",
				is_enabled: 1, //01
				imageUrl: "",
			});
		}else if(type === "edit") {
			setTempData(tempProduct)
		}
	}, [type, tempProduct])

    const submit = async () => {
        try {
            //create
			let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product`;
			let method = "post";

            //edit
            if (type === "edit") {
                //
				api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product/${tempProduct.id}`;
				method = "put";
			}

            const res = await axios[method](api, {
				data: tempData, //failed axios
			});

			//
			handleSuccessMessage(dispatch, res);
            closeProductModal();
            getProducts();  //
        } catch (err) {
			//
			handleErrorMessage(dispatch, err);
        }
    };

    const handleChange = (e) => {
		//console.log(e)
		const { value, name } = e.target;
		//console.log(typeof +e.target.checked);

		if (["price", "origin_price"].includes(name)) {
			//
			setTempData({
				...tempData,
				[name]: Number(value),
			});
		} else if (name === "is_enabled") {
			//is_enabled0 or 1
			//e.target.checked
			setTempData({
				...tempData,
				[name]: +e.target.checked, //e.target.checkedtrue/false+0/1
			});
		} else {
			setTempData({
				...tempData,
				[name]: value,
			});
		}
	};

	const uploadFile = async (e) => {
		const file = e.target.files[0];
		if (!file) return;
		const formData = new FormData();
		formData.append("file-to-upload", file);
		// console.log(formData);
		try {
			const imgUrl = await uploadImg(formData);
			setTempData({  //setTempData? setTempProduct?
				...tempData,
				imageUrl: imgUrl,
			});
		} catch (err) {
			console.log(err);
		}
	};

	const uploadImg = async (formData) => {
		const imgRes = await axios.post(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/upload`, formData);
		// console.log(imgRes.data.imageUrl);
		return imgRes.data.imageUrl;
	}
	// console.log(tempProduct);

	//modal

	return (
		<div id='productModal' className='modal fade' tabIndex='-1' aria-labelledby='exampleModalLabel' aria-hidden='true'>
			<div className='modal-dialog modal-lg'>
				<div className='modal-content'>
					<div className='modal-header'>
						<h1 className='modal-title fs-5' id='exampleModalLabel'>
							{type === "create" ? "" : ` ${tempData.title}`}
						</h1>
						<button type='button' className='btn-close' aria-label='Close' onClick={closeProductModal} />
					</div>
					<div className='modal-body'>
						<div className='row'>
							<div className='col-sm-4'>
								<div className='form-group mb-2'>
									<label className='w-100' htmlFor='image'>
										
										<input
											type='text'
											name='imageUrl'
											id='image'
											placeholder=''
											className='form-control'
										/>
									</label>
								</div>
								<div className='form-group mb-2'>
									<Input
										labelText=' '
										type='file'
										id='customFile'
										name='file-to-upload'
										placeholder=''
										value={""}
										onChange={uploadFile}
									/>
								</div>
								<img src={tempData.imageUrl} alt='' className='img-fluid' />
							</div>
							<div className='col-sm-8'>
								<div className='form-group mb-2'>
									<Input
										labelText=''
										type='text'
										id='title'
										name='title'
										placeholder=''
										value={tempData.title}
										onChange={handleChange}
									/>
								</div>
								<div className='row'>
									<div className='form-group mb-2 col-md-6'>
										<Input
											labelText=''
											type='text'
											id='category'
											name='category'
											placeholder=''
											value={tempData.category}
											onChange={handleChange}
										/>
									</div>
									<div className='form-group mb-2 col-md-6'>
										<Input
											labelText=''
											type='unit'
											id='unit'
											name='unit'
											placeholder=''
											value={tempData.unit}
											onChange={handleChange}
										/>
									</div>
								</div>
								<div className='row'>
									<div className='form-group mb-2 col-md-6'>
										<Input
											labelText=''
											type='number'
											id='origin_price'
											name='origin_price'
											placeholder=''
											value={tempData.origin_price}
											onChange={handleChange}
										/>
									</div>
									<div className='form-group mb-2 col-md-6'>
										<Input
											labelText=''
											type='number'
											id='price'
											name='price'
											placeholder=''
											value={tempData.price}
											onChange={handleChange}
										/>
									</div>
								</div>
								<hr />
								<div className='form-group mb-2'>
									<Input
										labelText=''
										type='text'
										id='description'
										name='description'
										placeholder=''
										value={tempData.description}
										onChange={handleChange}
									/>
								</div>
								<div className='form-group mb-2'>
									<Input
										labelText=''
										type='text'
										id='content'
										name='content'
										placeholder=''
										value={tempData.content}
										onChange={handleChange}
									/>
								</div>
								<div className='form-group mb-2'>
									<div className='form-check'>
										<label className='w-100 form-check-label' htmlFor='is_enabled'>
											
											<input
												type='checkbox'
												id='is_enabled'
												name='is_enabled'
												placeholder=''
												className='form-check-input'
												checked={!!tempData.is_enabled}
												onChange={handleChange}
											/>
										</label>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div className='modal-footer'>
						<button type='button' className='btn btn-secondary' onClick={closeProductModal}>
							
						</button>
						<button type='button' className='btn btn-primary' onClick={submit}>
							
						</button>
					</div>
				</div>
			</div>
		</div>
	);
};

export default ProductModal;
