import {useEffect, useReducer} from "react";
import {Outlet, useNavigate, Link} from "react-router-dom"
import axios from "axios";
import Message from "../../components/Message";
import { MessageContext, messageReducer, initState } from "../../store/messageStore";

const Dashboard = () => {
	const navigate = useNavigate();

	const reducer = useReducer(messageReducer, initState);
	//--> const [state, dispatch] = useReducer((state, action) => {}, initState);

	//cookie
	const logout = () => {
		document.cookie = "hexToken=;"; //"hexToken=;"
		navigate("/login");
	};

	//token
	const token = document.cookie
		.split("; ") //cookies(; )()
		.find((row) => row.startsWith("hexToken=")) //startWith "hexToken"()
		?.split("=")[1]; //=()--> [0]"hexToken"[1]token

	// axios.defaults.headers.common["Authorization"] = AUTH_TOKEN;  //axios
	// -->axiosheaders
	axios.defaults.headers.common["Authorization"] = token; //03 token

	//token
	useEffect(() => {
		//token
		if (!token) {
			return navigate("/login");  //returntoken
		}
		//token
		(async () => {
			try {
				await axios.post(`/v2/api/user/check`);
			} catch (err) {
				console.log(err);
				if (!err.response.data.success) {
					navigate("/login");
				}
			}
		})();
	}, [navigate, token]);

	return (
		<>
			<MessageContext.Provider value={reducer}>
				<Message />
				<nav className='navbar navbar-expand-lg bg-dark'>
					<div className='container-fluid'>
						<p className='text-white mb-0'>HEX EATS </p>
						<button
							className='navbar-toggler'
							type='button'
							data-bs-toggle='collapse'
							data-bs-target='#navbarNav'
							aria-controls='navbarNav'
							aria-expanded='false'
							aria-label='Toggle navigation'
						>
							<span className='navbar-toggler-icon' />
						</button>
						<div className='collapse navbar-collapse justify-content-end' id='navbarNav'>
							<ul className='navbar-nav'>
								<li className='nav-item'>
									<button type='button' className='btn btn-sm btn-light' onClick={logout}>
										
									</button>
								</li>
							</ul>
						</div>
					</div>
				</nav>
				<div className='d-flex' style={{ minHeight: "calc(100vh - 56px)" }}>
					<div className='bg-light' style={{ width: "200px" }}>
						<ul className='list-group list-group-flush'>
							<Link className='list-group-item list-group-item-action py-3' to='/admin/products'>
								<i className='bi bi-cup-fill me-2' />
								
							</Link>
							<Link className='list-group-item list-group-item-action py-3' to='/admin/coupons'>
								<i className='bi bi-ticket-perforated-fill me-2' />
								
							</Link>
							<Link className='list-group-item list-group-item-action py-3' to='/admin/orders'>
								<i className='bi bi-receipt me-2' />
								
							</Link>
						</ul>
					</div>
					<div className='w-100'>
						{/* Products */}
						{token && <Outlet />}
						{/* useEffect()token */}
						{/* Products end */}
					</div>
				</div>
			</MessageContext.Provider>
		</>
	);
}

export default Dashboard;