import { useEffect, useState, useRef, useContext } from 'react';
import axios from "axios";
import { Modal } from "bootstrap";

import ProductModal from '../../components/ProductModal';
import DeleteModal from '../../components/DeleteModal';
import Pagination from '../../components/Pagination';
import { deleteSuccessMessage, MessageContext } from './../../store/messageStore';


const AdminProducts = () => {
	const [products, setProducts] = useState([]);
	const [pagination, setPagination] = useState({});

	//typemodal
	const [type, setType] = useState("create"); //createedit

	//()
	const [tempProduct, setTempProduct] = useState({});

	const [, dispatch] = useContext(MessageContext);

	//bootstrap modal
	const productModal = useRef(null);
	const deleteModal = useRef(null);


	//modal(modalmodal)
	//const myModalAlternative = new bootstrap.Modal('#myModal', options)
	//-->mdnbootstrap
	//const myModalAlternative = new Modal('#myModal', options)
	useEffect(() => {
		productModal.current = new Modal("#productModal", {
			backdrop: "static",
		});
		deleteModal.current = new Modal("#deleteModal", {
			backdrop: "static",
		});
		getProducts();
	}, []);

	//
	const getProducts = async (page = 1) => {
		try {
			//B
			const productRes = await axios.get(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/products?page=${page}`); //04 APItoke
			// console.log(productRes);
			setProducts(productRes.data.products);
			setPagination(productRes.data.pagination);
		} catch (err) {
			console.log(err);
		}
	};

	//
	const deleteProduct = async (id) => {
		try {
			const res = await axios.delete(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/product/${id}`);
			console.log(res);
			if (res.data.success) {
				//
				deleteSuccessMessage(dispatch, res);
				deleteModal.current.hide();
				getProducts(); //
			}
		} catch (err) {
			console.log(err);
		}
	};

	//ProductModal
	const openProductModal = (type, product) => {
		setType(type);
		setTempProduct(product);
		productModal.current.show();
	};
	const closeProductModal = () => {
		productModal.current.hide();
	};

	//DeleteModal
	const openDeleteModal = (product) => {
		setTempProduct(product);
		deleteModal.current.show();
	};
	const closeDeleteModal = () => {
		deleteModal.current.hide();
	};

	return (
		<div className='p-3'>
			<ProductModal
				closeProductModal={closeProductModal}
				getProducts={getProducts}
				type={type}
				tempProduct={tempProduct}
			/>
			<DeleteModal 
                close={closeDeleteModal} 
                text={tempProduct.title} 
                handleDelete={deleteProduct} 
                id={tempProduct.id} 
            />
			<h3></h3>
			<hr />
			<div className='text-end'>
				<button type='button' className='btn btn-primary btn-sm' onClick={() => openProductModal("create", {})}>
					
				</button>
			</div>
			<table className='table'>
				<thead>
					<tr>
						<th scope='col'></th>
						<th scope='col'></th>
						<th scope='col'></th>
						<th scope='col'></th>
						<th scope='col'></th>
					</tr>
				</thead>
				<tbody>
					{products.map((item) => (
						<tr key={item.id}>
							<td>{item.category}</td>
							<td>{item.title}</td>
							<td>{item.price}</td>
							<td>{item.is_enabled ? "" : ""}</td>
							<td>
								<button type='button' className='btn btn-primary btn-sm' onClick={() => openProductModal("edit", item)}>
									
								</button>
								<button
									type='button'
									className='btn btn-outline-danger btn-sm ms-2'
									onClick={() => openDeleteModal(item)}
								>
									
								</button>
							</td>
						</tr>
					))}
				</tbody>
			</table>

			{/* Pagination */}
			<Pagination pagination={pagination} changePage={getProducts} />
		</div>
	);
}

export default AdminProducts;

